// Ecommerce Pro Cards
// Collection card
.collection-card {
  .stripe {
    position: absolute;
    bottom: 3rem;
    width: 100%;
    text-align: center;
    padding: 1.2rem;
    &.dark {
      background-color: $rgba-black-strong;
      a p {
        color: map-get($grey, "lighten-3");
      }
    }
    &.light {
      background-color: $rgba-white-strong;
      a p {
        color: map-get($grey, "darken-3");
      }
    }
    a {
      p {
        padding: 0;
        margin: 0;
        letter-spacing: 4px;
        font-size: 1rem;
      }
    }
  }
}

.rating {
  list-style-type:none;
  padding:0;
  li {
    display: inline-block;
  }
}
// When icon doesn't need padding
.no-padding {
  .fa {
    padding: 0;
  }
}

// Quick look modal
.ql-modal,
.product-panel {
  .modal-dialog {
    max-width: none;
    width: auto;
    margin: 2rem;
  }
  .modal-header {
    text-align: center;
  }
  .price {
    .badge {
      background-color: $rgba-red-strong;
      font-size: 1.4rem;
      display: inline-block;
      margin-left: 1rem;
    }
    p {
      display: inline-block;
      font-size: 1.3rem;
      .price-before {
        color: black;
        text-decoration: line-through;
        margin-left: 0.5rem;
      }
      .price-after {
        font-size: 1.7rem;
        color: green;
      }
    }
  }
  .media img {
    max-width: 60px;
  }
}

@media (max-width: 1200px) {
  .ql-modal {
    .modal-dialog {
      margin: 0;
    }
    padding-left: 0;
  }
}

.wishlist {
  @extend .btn-sm;
  background-color: $danger-color;
}

